@font-face {
    font-family: "BunchBlossomsPersonalUse";
    src: url("./../Navbar/BunchBlossomsPersonalUse-0nA4.ttf") format("truetype");
}


/* The switch - the box around the slider */
.switch {
    display: inline-block;
    width: 60px;
    height: 34px;
    position: absolute;
    top: 5px;
    right: 20px;
    z-index: 1;
}

/* Hide default HTML checkbox */
.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
.slider {
    font-size: small;
    font-family: BunchBlossomsPersonalUse;
    color: dimgrey;
    font-weight: bold;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
    border: 1px solid grey;
}

.slider:before {
    box-shadow: 0px 0px 30px white;
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: grey;
    -webkit-transition: .4s;
    transition: .4s;
}

input:checked + .slider {
    background-color: lightgrey;
}

input:focus + .slider {
    box-shadow: 0 0 1px linen;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}