@font-face {
  font-family: "BunchBlossomsPersonalUse";
  src: url("BunchBlossomsPersonalUse-0nA4.ttf") format("truetype");
}

.logoWrapper {
  padding-left: 60px;
}

.links {
  flex-grow: 0;
}

.authorText {
  font-family: BunchBlossomsPersonalUse;
  text-shadow: 0px 0px 30px white !important;
  color: black !important;
}

.navDiv {
  display: flex !important;
  justify-content: space-between !important;
}

nav {
  background-image: url("background.jpg");
}

.nav a {
  color: black !important;
  font-size: larger !important;
  font-weight: bolder !important;
  text-shadow: 0px 0px 5px white !important;
  transition: text-shadow 1s ease-in, color 2s !important;
}

.nav a:hover {
  text-shadow: 0px 0px 15px white !important;
}

.nav a:visited {
  color: #1e2125;
}

